
<template>
  <div class="container">
    <div class="mt-3">
      <h3><i class="fas fa-cogs"></i> Permisos</h3>
    </div>

    <div class="row mt-3">
      <div class="col-12 col-md-2">
        <label>Usuario:</label>
      </div>
      <div class="col-12 col-md-10">
        <select
          v-model="usuarioSeleccionado"
          v-on:change="cambiarUsuario"
          class="form-control"
        >
          <option
            v-for="usuario in Usuarios"
            v-bind:key="usuario.codigo"
            v-bind:value="usuario"
          >
            {{ usuario.nombreCompleto }}
          </option>
        </select>
      </div>
    </div>
    <div class="row mt-2">
      <div class="col-12 col-md-2">
        <label>Modulo:</label>
      </div>
      <div class="col-12 col-md-10">
        <select
          v-model="moduloSeleccionado"
          v-on:change="cambiarModulo"
          class="form-control"
        >
          <option
            v-for="modulo in Modulos"
            v-bind:key="modulo.idOpcion"
            v-bind:value="modulo"
          >
            {{ modulo.opcion }}
          </option>
        </select>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <div class="table-responsive table-sm my-2 tableFixHead">
          <table class="table table-striped">
            <thead>
              <tr>
                <th style="width: 340px;">Función</th>
                <th style="width: 80px; text-align:center">Lectura</th>
                <th style="width: 80px; text-align:center">Escritura</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(funcion, index) in Funciones" :key="index">
                <td v-text="funcion.opcion"></td>
                <td style="text-align: center">
                  <b-check v-model="funcion.lectura" />
                </td>
                <td style="text-align: center">
                  <b-check v-model="funcion.escritura" />
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <!-- botones -->
    <div class="row mt-3">
      <!-- <div class="offset-sm-4 col-lg-8 "> -->
      <div class="col-12 offset-sm-4 col-sm-4 col-lg-4">
        <button class="btn btn-danger col-12 mb-1" @click="Cancelar">
          Cancelar
        </button>
      </div>
      <div class="col-12 col-sm-4 col-lg-4">
        <button class="btn btn-success col-12 mb-1" @click="GuardarFunciones">
          <i class="fa fa-save"></i> Guardar
        </button>
      </div>
      <!-- </div> -->
    </div>

    <!-- <b-row class="mt-5">
            <b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
            <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                <b-button @click="GuardarFunciones" block variant="success" class="mr-2 mt-2">
                    <i class="fa fa-save"></i> Guardar
                </b-button>
            </b-col>
            <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
                <b-button @click="Cancelar" block variant="danger" class="mr-2 mt-2">
                    Cancelar
                </b-button>
            </b-col>
        </b-row> -->
  </div>
</template>

<script>
import Swal from "sweetalert2";
import servicio from "@/services/servicio-configuracion";

export default {
  data() {
    return {
      Usuarios: [],
      usuarioSeleccionado: [],
      Modulos: [],
      moduloSeleccionado: [],
      Funciones: [],
      procede: true,


      tokenWebAdmin: {
        token:null,
        expiresAt:null,
        user:null,
        permisos:null
      }
    };
  },
  components: {},
  async mounted() {
    // let resultado = await this.verificaPermiso("permisos")
    // if(resultado) {
    //     // console.log(resultado);
    //     await this.ObtenerUsuarios();
    //     await this.ObtenerModulos();
    //     await this.ObtenerFunciones();
    // }
    // console.log(this.usuarioGlobal);
    await this.ObtenerUsuarios();
    await this.ObtenerModulos();
    await this.ObtenerFunciones();
  },
  created() {},
  computed: {},
  methods: {
    async ObtenerUsuarios() {
      this.$loading(true);
      await servicio
        .obtenerUsuarios()
        .then((response) => {
          this.Usuarios = response.data;
          if (this.Usuarios.length > 0) {
            this.usuarioSeleccionado = this.Usuarios[0];
          }
          this.$loading(false);
        })
        .catch((error) => {
          console.log(error);
          this.$loading(false);
        });
    },

    cambiarUsuario: function () {
      this.ObtenerFunciones();
    },

    async ObtenerModulos() {
      this.$loading(true);
      await servicio
        .obtenerModulos()
        .then((response) => {
          this.Modulos = response.data;
          this.moduloSeleccionado = this.Modulos[0];
          this.$loading(false);
        })
        .catch((error) => {
          console.log(error);
          this.$loading(false);
        });
    },

    cambiarModulo: function () {
      this.ObtenerFunciones();
    },

    async ObtenerFunciones() {
      this.Funciones = [];
      this.$loading(true);
      await servicio
        .obtenerFunciones(
          this.moduloSeleccionado.idOpcion,
          this.usuarioSeleccionado.codigo
        )
        .then((response) => {
          this.Funciones = response.data;
          this.$loading(false);
        })
        .catch((error) => {
          console.log(error);
          this.$loading(false);
        });
    },

    async GuardarFunciones() {
      await this.FuncionesGuardar();
      await this.ObtenerPermisos();
      
    },

    Cancelar() {
      window.history.back();
    },

    async FuncionesGuardar() {
      let r = await Swal.fire({
        // title: "¡ATENCIÓN!",
        text:
          "¿Desea guardar los permisos del usuario: " +
          this.usuarioSeleccionado.nombreCompleto +
          "?",
        showCancelButton: true,
        icon: "question",
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Aceptar",
        cancelButtonText: "Cancelar",
      });

      if (r.value) {
        this.$loading(true);
        var myJSON = JSON.stringify(this.Funciones);
        await servicio
          .guardarFunciones(
            myJSON,
            this.moduloSeleccionado.idOpcion,
            this.usuarioSeleccionado.codigo,
            this.usuario.idUsuario
          )
          .then((response) => {
            if (response.value) {

              console.log(this.usuarioSeleccionado);
              Swal.fire(
                "¡EXITO!",
                "Se modificaron los permisos al usuario: " +
                  this.usuarioSeleccionado.nombreCompleto,
                "success"
              );

              this.$loading(false);
            }
          })
          .catch((error) => {
            console.log(error);
            this.$loading(false);
          });

          
      }
    },

    async ObtenerPermisos() {
      if(this.usuarioSeleccionado.codigo === this.usuario.idUsuario) {
        
      this.$loading(true);
            
       await servicio.obtenerPermisos(this.usuario.idUsuario)
              .then((r) => {
                if (r.value) {
                  window.localStorage.removeItem("tokenWebAdmin");
                  this.tokenWebAdmin.token = r.data.accessToken.accessToken
                  this.tokenWebAdmin.expiresAt = r.data.accessToken.exp
                  this.tokenWebAdmin.user = JSON.stringify(r.data.user)
                  this.tokenWebAdmin.permisos = JSON.stringify(r.data.permisos) 
                  // this.tokenWebAdmin.empresas = JSON.stringify(r.data.empresas)
                  var decode = decodeURIComponent(encodeURI(this.tokenWebAdmin.user))
                  this.tokenWebAdmin.user = decode
                  window.localStorage.tokenWebAdmin = JSON.stringify(this.tokenWebAdmin)
                  this.$loading(false);
                }
              })
              .catch((error) => {
                console.log(error);
                this.$loading(false);
              });
      this.$loading(false);

        location.reload();
      }
    }
  },
};
</script>

<style scoped>
.margen {
  border: 2px solid red;
}

.margen2 {
  border: 2px solid blue;
}

table {
  border: 2px solid #1860e6;
  border-radius: 3px;
  background-color: #fff;
}

th {
  background-color: #1860e6;
  color: rgb(255, 255, 255);
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* td {
		background-color: #f9f9f9;
	} */

.icon-trash {
  color: var(--color-blanco);
}
.icon-trash:hover {
  color: rgb(141, 19, 19);
  cursor: pointer;
}
.circle-trash {
  background-color: #c0c8ccf5 !important;
  border-radius: 50%;
}

.tableFixHead {
  overflow-y: auto;
}

.tableFixHead thead th {
  position: sticky;
  top: 0;
}

.error {
  border: 2px solid red;
}
</style>