import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                  C O N F I G U R A C I Ó N
//............................................................
servicio.obtenerUsuarios = () => {

    return base.get(`/configuracion/usuarios`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.obtenerModulos = () => {

    return base.get(`/configuracion/modulos/`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.obtenerFunciones = (codModulo, codUsuario) => {

    return base.get(`/configuracion/funciones/${codModulo}/${codUsuario}`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.guardarFunciones = (funciones, codModulo, codUsuario, codUsuarioGlobal) => {

    return base.post(`/configuracion/funciones/guardar/${codModulo}/${codUsuario}/${codUsuarioGlobal}`, funciones, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.obtenerPermisos = (codUsuario) => {

    return base.post(`/configuracion/obtener-permisos/${codUsuario}`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

export default servicio